body{
    background-color: #3A2B32;
    font-family: Arial, Helvetica, sans-serif;
}
.container, .links{
    background-color: #3A2B32;
    color: white;
}
.links{
    text-decoration: none;
    padding: 10px;
}
.links:hover {
    background-color: #8A6C78;
    border-radius: 15px;   
}
.container{
    display: flex;
    flex-direction: column;
}
.navMenu{
    display: flex;
    justify-content: space-around;
    padding: 5px;
    font-weight: bold;
    margin-bottom: 10px;
}
.footer{
    text-align: center;
    padding: 10px;
    color: white;
}
.logOutBtn{
    background-color: #8A6C78; 
    color: white; 
    padding: 7px 10px; 
    border: none;
    border-radius: 8px; 
    cursor: pointer;
}
.logedD{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 5px;
}