.errorContainer{
    color: white;
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
    margin-top: 30px;
}
.errorLink{
    color: #3A2B32;
    font-weight: bold;
    background-color: #E6CFC9;
    padding: 15px;
    border-radius: 10px;
}
.errorActions{
    padding: 50px;
}
.loading{
    height: 20vh;
}
.loadingContainer{
    display: flex;
    margin-top: 100px;
    justify-content: center;
}
.notfoundComponent{
    color: white;
    text-align: center;
    margin-top: 100px;
    margin-bottom: 50px;
}
.notFoundContainer{
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 40px;
}
.notFoundList{
    text-align: left;
    align-content: center;
}
.linkNotFound{
    color: #C39B8F;
}