.container {
    background-color: rgba(101, 67, 81, 0.5);
    color: white;
    padding: 50px;
    text-align: center;
    margin: 10px 40px;
    display: flex;
    flex-direction: column;
    border-radius: 15px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2); 
}

.regLogForm {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.headerHomepage {
    background: linear-gradient(135deg, rgba(138, 108, 120, 0.8), rgba(101, 67, 81, 0.8)); 
    padding: 20px 30px; 
    border-radius: 10px; 
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); 
}
.regLogForm {
    background: rgba(255, 255, 255, 0.8); 
    border-radius: 15px; 
    padding: 20px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column; 
    max-width: 400px; 
    margin: auto; 
}
.regLogForm label {
    margin-bottom: 5px;
    font-weight: bold; 
    color: #3A2B32; 
}
.regLogForm input {
    padding: 10px; 
    border: 1px solid #3A2B32;
    border-radius: 8px;
    margin-bottom: 15px; 
}
.regLogForm input:focus {
    border-color: #8A6C78; 
}
.regLogBtn {
    background-color: #8A6C78; 
    color: white; 
    padding: 10px; 
    border: none;
    border-radius: 8px; 
    cursor: pointer; 
}  
.regLogBtn:hover {
    background-color: #7a5b69;
}
.authMessage {
    color: red;
    margin-top: 10px;
}
.nameLogedIn{
    color: #a55c79;
}