.container {
    background-color: rgb(224, 204, 165);
    color: #3A2B32;
    margin: 10px 40px;
    padding: 50px;
    border-radius: 10px;
}

.recipesWelcome {
    margin-bottom: 30px;
}

.recipeCollection {
    font-size: 2.5rem;
    margin-bottom: 10px;
}

.selectKitche {
    padding: 8px;
    margin-right: 10px;
    border-radius: 5px;
    background-color: rgba(241, 191, 89, 0.692);
    border: 1px solid #3A2B32;
}

.recipeCard button, 
.showAllBtn, 
.searchShowBtn {
    background-color: #3A2B32;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    cursor: pointer;
    margin-top: 0;
    font-size: 14px;
    height: 40px; /* Set a fixed height for buttons for uniformity */
}

.recipeCard button:hover,
.showAllBtn:hover, 
.searchShowBtn:hover {
    background-color: #6A4A3D;
}

.recipeCard button:focus, 
.showAllBtn:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(58, 43, 50, 0.5);
}

.instructions {
    margin-top: 10px;
    font-weight: bold;
}

.recipeImage {
    width: 15vw;
}

.searchContainer {
    display: flex;
    align-items: center;
    gap: 15px;
}

.searchBar {
    width: 50vw;
    padding: 10px;
    border: 1px solid white;
    border-radius: 4px;
    font-size: 16px;
    height: 40px; /* Set a fixed height for the search bar for uniformity */
}

.recipesBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
}

.showAllBtn {
    width: 20vw; /* Adjust as necessary */
}
  
.recipeList {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    justify-content: space-evenly;
}

.recipeCard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 18vw;
    border: #3A2B32 1px solid;
    border-radius: 5px;
    padding: 5px; 
    margin-bottom: 20px;
}

.filterOptions {
    display: flex;
    align-items: center;
}
