.container {
    display: flex;
    flex-direction: column;
    background-color: #E6CFC9;
    margin: 10px 40px;
    color: #3A2B32;
    padding: 50px;
    border-radius: 8px; 
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); 
}
.searchBar {
    display: flex;
    justify-content: space-between;
    align-items: center; 
    margin-bottom: 20px; 
}
.searchBarInp {
    width: 50vw;
    padding: 10px;
    border: 1px solid white;
    border-radius: 4px; 
    font-size: 16px;
}
.searchBar button{
    padding: 12px;
    background-color: #3A2B32; 
    color: white; 
    border: none; 
    border-radius: 4px; 
    cursor: pointer; 
}
.addBtn{
    
    padding: 12px;
    background-color: #3A2B32; 
    color: white; 
    border: none; 
    border-radius: 4px; 
    cursor: pointer; 
}
.searchBar button:hover {
    background-color: #5B4E52; 
}
.showBtn {
    display: flex;
    gap: 10px;
}
.searchForm {
    display: flex;
    gap: 10px;
}
.bookCover{
    width: 5vw;
}
.allFilteredBooks {
    display: flex;
    flex-wrap: wrap; 
    gap: 20px; 
    justify-content: flex-start; 
}

.booksElement {
    flex: 0 0 calc(25% - 20px); 
    box-sizing: border-box; 
    display: flex;
    flex-direction: column;
    align-items: center; 
    border: #3A2B32 1px solid;
    padding: 10px;
}